* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.header {
  width: 100%;
  height: 64px;
  z-index: 2;
  background-color: #b0e0e6;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: auto;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form {
  width: 224px;
}

.input {
  width: 224px;
  height: 38px;
  border: 0;
  border-radius: 10px;
  padding-left: 10px;
  position: absolute;
  top: 12px;
}

.btn-search {
  cursor: pointer;
  height: 24px;
  color: #faebd7;
  background-color: #a9a9a9;
  border: 0;
  border-radius: 6px;
  position: relative;
  bottom: 2px;
  right: -150px;
}

.btn-load {
  cursor: pointer;
  height: 24px;
  color: #faebd7;
  background-color: #a9a9a9;
  border: 0;
  border-radius: 6px;
  position: fixed;
  bottom: 50px;
  left: 46%;
}

.btn-load, .btn-search {
  color: #fff;
  background-image: linear-gradient(45deg, #708090 0%, #a9a9a9 50%, #000 190%);
  background-position: 100% 0;
  background-size: 200% 200%;
  text-decoration: none;
  transition: all .5s;
  display: inline-block;
  box-shadow: 0 2px 8px rgba(0, 40, 120, .35);
}

.btn-load:hover, .btn-search:hover {
  background-position: 0 0;
  box-shadow: 0 0 rgba(0, 40, 120, 0);
}

.hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 104px;
  display: flex;
}

img {
  height: 350px;
  max-width: 350px;
  object-fit: cover;
}

.all-cards {
  margin-right: 40px;
}

.photo-card {
  z-index: 1;
  max-width: 350px;
  cursor: pointer;
  border: .5px solid #f4f4fd;
  margin-bottom: 40px;
  transition-property: box-shadow, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

.photo-card:hover {
  z-index: 1;
  border: .5px solid #f4f4fd;
  transform: scale(1.1);
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.all-cards:nth-child(3n+3) {
  margin-right: 0;
}

.info {
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 22px;
  padding-bottom: 22px;
  display: flex;
}

.info-item {
  margin: 0;
}

.info-item:nth-child(2n+1) {
  margin-left: 22px;
  margin-right: 60px;
}

